import Vue from 'vue'
import Vuex from 'vuex'
import {
	mjlow,
	mjSecend,
	mjBase,
	arCode
} from "@/commit/api.js"
Vue.use(Vuex)
let lifeData = {}

// lifeData
try {
	lifeData = uni.getStorageSync('lifeData')
} catch (e) {

}
// state
let saveStateKeys = ['vuex_user']

// 
const saveLifeData = function(key, value) {
	// 
	if (saveStateKeys.indexOf(key) != -1) {
		// lifeData
		let tmpLifeData = uni.getStorageSync('lifeData')
		// 
		tmpLifeData = tmpLifeData ? tmpLifeData : {},
			tmpLifeData[key] = value
		// 
		uni.setStorageSync('lifeData', tmpLifeData)
	}
}

const store = new Vuex.Store({
	state: {
		uerInfo: {},
		hasLogin: false,
		imageData: '',
		imageIsLoad: false,
		codeImgData: '',
		codeIsLoad: false,

		// lifeDatastate
		// vuex_
		vuex_user: lifeData.vuex_user ? lifeData.vuex_user : {
			name: ''
		},

		// vuex_versionlifeData.vuex_version
		// app
		vuex_version: "1.0.0",
		// 
		vuex_custom_nav_bar: true,
		// 
		vuex_status_bar_height: 0,
		// 
		vuex_custom_bar_height: 0
	},
	mutations: {
		getImageData(){
			
		},
		changeimageLoad(state,data) { 
			state.imageData = data
			// state.codeIsLoad = false
		},
		changeLoad(state) { 
			state.imageIsLoad = false
			state.codeIsLoad = false
		},
		$tStore(state, payload) {
			// stateuser.info.score = 1
			let nameArr = payload.name.split('.')
			let saveKey = ''
			let len = nameArr.length
			if (len >= 2) {
				let obj = state[nameArr[0]]
				for (let i = 1; i < len - 1; i++) {
					obj = obj[nameArr[i]]
				}
				obj[nameArr[len - 1]] = payload.value
				saveKey = nameArr[0]
			} else {
				// 
				state[payload.name] = payload.value
				saveKey = payload.name
			}

			// 
			saveLifeData(saveKey, state[saveKey])
		},
		login(state, provider) { // 
			state.hasLogin = true
			state.uerInfo.token = provider.token
			state.uerInfo.userName = provider.data.name
			uni.setStorageSync('token', provider.token)
			uni.setStorageSync('userInfo', provider.data)
		},
		logout(state) { //  
			state.hasLogin = false
			state.uerInfo = {}
			uni.clearStorage()
		},
		loginStatus(state) { //
			let token = uni.getStorageSync('token');
			let data = uni.getStorageSync('userInfo');
			let inviteUrl = window.location.href.split('?')[0] 
		}
	},
	actions: {
		codeDrw({
			commit,
			state
		}, drwData) { 
			state.codeIsLoad = true
			state.codeImgData = '';
			 
			if (drwData.prompt == '' || drwData.content == '') {
				uni.showToast({
					title: '',
					icon: 'none'
				})
				return false
			}
			uni.showToast({
				title: '',
				icon: 'none'
			})
			drwData.socketId = uni.getStorageSync('SOCKET_ID')
			arCode(drwData).then((res) => { 
				state.codeIsLoad = false;
				if (!res.image_url) {
					uni.showToast({
						title: res.detail,
						icon: 'none'
					})
				} else {
					// assuming you have a state for imageRes  
					state.imageData = res
				}
			})
		},
		mjImageDrw({
			commit,
			state
		}, drwData) {
			state.imageIsLoad = true
			state.imageData = '';
			drwData.socketId = uni.getStorageSync('SOCKET_ID')
			mjlow(drwData).then((res) => { 
				if (res.code == 999) {
					uni.showToast({
						title: res.msg,
						icon: 'none'
					})
					state.imageIsLoad = false;
					return
				}
				if (!res.data.image_url) {
					state.imageIsLoad = true;
					uni.showToast({
						title: res.msg,
						icon: 'none'
					})
				} else {
					state.imageIsLoad = false;
					// assuming you have a state for imageRes  
					state.imageData = res.data
				}
			})
		}
	}
})
export default store