import configdata from './config'
import store from 'vuex';
let socketOpen = false;
let socketMsgQueue = [];
let scoketURL = configdata.SOOCKETURL
let sysInfo = uni.getSystemInfoSync() 

function connectWebSocket() {
	uni.connectSocket({
		url: scoketURL, //  WebSocket URL,
		success: function() {
			console.log('WebSocket : ' + scoketURL + 'socket server');
		},
		fail: function(error) {
			console.log(error)
			console.error('WebSocket ' + scoketURL + ' :', error);
		}
	});

	uni.onSocketOpen(function() {
		socketOpen = true;
		// 
		sendSocketMessage(JSON.stringify(sysInfo));
	});

	uni.onSocketMessage(function(res) {
		console.log(res.data.includes('SOCKET_ID&'))
		if (res.data.includes('SOCKET_ID&')) {
			uni.setStorageSync('SOCKET_ID', res.data.split('&')[1])
		}
		if (res.data.includes('AI&')) {
			uni.setStorageSync('IMAGE_DATA', res.data.split('&')[1])
			// this.$store.commit('changeimageLoad', res.data);
		}

		console.log(':', res.data);

		// 
	});

	uni.onSocketClose(function() {
		console.log('WebSocket ');
		socketOpen = false;

		// 
		//  WebSocket
		connectWebSocket();
	});

	uni.onSocketError(function(error) {
		console.error('WebSocket :', error);

		// 
	});
}

function sendSocketMessage(message) {
	if (socketOpen) {
		uni.sendSocketMessage({
			data: message
		});
	} else {
		socketMsgQueue.push(message);
	}
}

module.exports = {
	connectWebSocket: connectWebSocket,
	sendSocketMessage: sendSocketMessage
};